.addMessage {
    position: absolute;
    min-width: 55%;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    top: 90%;
    margin-top: 10px;
}

.addMessage::before {
    content: "";
    position: absolute;
    top: -10px;
    left: 0;
    right: 0;
    height: 1px;
    background: linear-gradient(
            90deg, rgba(217, 217, 217, 0) 0%,
            rgba(217, 217, 217, 0.4) 2%,
            rgba(217, 217, 217, 0.4) 98%,
            rgba(217, 217, 217, 0) 100%);
}

.messageInput {
    background: transparent;
    border-radius: 8px;
    width: 95%;
    margin-right: 5px;
}

.sendBtn {
    width: 30px;
}